<template>
  <div id="footbar">
    <div class="link-box">
      <router-link :to="l[1]" v-for="l in links" :key="l[1]">{{l[0]}}</router-link>
    </div>
    <div class="brand" @click="eggshell">
      <logo :height="32"></logo>
      <a :href="school[0].url" @click="eggshell">{{school[0].name}}</a>
    </div>
  </div>
</template>

<script>
  import logo from '../../components/logo/logo.vue'
  export default {
    name: 'footbar',
    components: {
      logo
    },
    data: function () {
      return {
        school: [
          {
            name: '',
            url: 'http://math.tongji.edu.cn/'
          },
          {
            name: '',
            url: 'http://sse.tongji.edu.cn/en'
          }
        ],
        links: [
          ['', '/'],
          ['', '/about'],
          ['', '/forum'],
          ['', '/copyright'],
          ['ICP160000xx', '/beian']
        ]
      }
    },
    methods: {
      eggshell: function () {
        this.school.reverse()
      }
    }
  }
</script>

<style lang='sass' rel='stylesheet/sass'>
  @import '../../style/footbar'
</style>



